<template>
  <div>
    <h3>elementUI</h3>

    <div class="demo1">
      <p></p>
      <el-upload
        class="upload-demo"
        action="https://jsonplaceholder.typicode.com/posts/"
        :on-change="handleChange"
        :file-list="fileList">
        <el-button size="small" type="primary"></el-button>
        <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
      </el-upload>
    </div>

    <div class="demo2">
      <p></p>
      <p></p>
      <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        list-type="picture-card"
        :on-preview="handlePictureCardPreview"
        :on-remove="handleRemove">
        <i class="el-icon-plus"></i>
      </el-upload>
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="">
      </el-dialog>
    </div>

    <div class="demo3">
      <h3></h3>
      <p>vue-cropper,github <a href="https://github.com/xyxiao001/vue-cropper">https://github.com/xyxiao001/vue-cropper</a>
      </p>
      <div class="avatar-box">
        <i class="el-icon-plus icon" @click="upload"></i>
        <input class="upload" id="upload" type="file" accept="image/*" style="display: none"
               @change="uploadFile($event)">
        <img :src="img" alt="" class="img" :style="{display: 'none'}">
        <button class="uploadBtn"></button>
      </div>
      <el-dialog
        title=""
        :visible.sync="imgUploadBox"
        width="35%"
        :before-close="beforeClose">
        <div>
          <!---->
          <div :style="{width: '400px', height: '400px'}">
            <VueCropper ref="cropper"
                        :img="img"
                        :outputSize="1"
                        :outputType="outputType"
                        :autoCrop="true"
                        :autoCropWidth="200"
                        :autoCropHeight="200"
                        @cropMoving="cropMove"
                        :centerBox="true"
            ></VueCropper>
          </div>
          <!---->
          <div class="reading">
            <h3 :style="{marginTop: '0'}"></h3>
            <div>
              <img :src="readingImg" alt="">
            </div>
          </div>
        </div>
        <span slot="footer">
        <el-button type="info" @click="cancel"></el-button>
        <el-button type="primary" @click="uploadImg"></el-button>
      </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import {VueCropper} from 'vue-cropper'

  export default {
    name: "UploadFile",
    data() {
      return {
        fileList: [{
          name: 'food.jpeg',
          url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        }, {
          name: 'food2.jpeg',
          url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        }],
        dialogImageUrl: '',
        dialogVisible: false,
        img: '', //
        readingImg: '', //
        outputType: 'jpeg', //
        imgUploadBox: false
      }
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      handleChange(file, fileList) {
        this.fileList = fileList.slice(-3);
      },
      //
      upload() {
        this.$nextTick(() => {
          const upload = this.$el.querySelector(".upload").click()
        })
      },
      //input type=file
      uploadFile(event) {
        this.imgUploadBox = true
        const data = this.getObjectUrl(event.target.files[0])
        this.img = data
      },
      getObjectUrl(file) { //blob
        let url = null;
        //blob:http://localhost:8080/3bf759b8-1c9b-4888-92c8-395d8357cf59
        if (window.createObjectURL != undefined) {
          url = window.createObjectURL(file);
        } else if (window.URL != undefined) { // mozilla(firefox)
          url = window.URL.createObjectURL(file);
        } else if (window.webkitURL != undefined) { // webkit or chrome
          url = window.webkitURL.createObjectURL(file);
        }
        return url;
      },
      beforeClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {
          });
      },
      //
      cropMove() {
        // blob
        this.$refs.cropper.getCropData((data) => {
          this.readingImg = data
        })
      },
      //
      cancel() {
        this.img = ''
        this.readingImg = ''
        this.imgUploadBox = false
      },
      //
      uploadImg() {
        if (this.readingImg) {
          //
          alert("")
          this.img = this.readingImg
          this.readingImg = ''
          this.imgUploadBox = false
          //
          this.$nextTick(() => {
            this.$el.querySelector('.icon').style.display = 'none'
            this.$el.querySelector('.img').style.display = 'block'
          })
        }
      }
    },
    components: {
      VueCropper
    }
  }
</script>

<style scoped>

  .avatar-box {
    position: relative;
    display: flex;
    width: 150px;
    height: 150px;
    border: 1px dashed #5F4B8B;
    text-align: center;
  }

  .avatar-box i {
    font-size: 30px;
    line-height: 150px;
    margin: auto;
    cursor: pointer;
  }

  .avatar-box .uploadBtn {
    width: 100%;
    height: 40px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: none;
    cursor: pointer;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .reading {
    position: absolute;
    top: 85px;
    right: 30px;
  }

  .reading div {
    width: 200px;
    height: 200px;
    border: 1px dashed #5F4B8B;
  }

</style>